<template>
  <mu-container>
  	
		<center v-show='loading'>
			<mu-circular-progress  class="demo-circular-progress" :size="36" ></mu-circular-progress>	
		</center>	
		<div class="idcard" v-show='!loading'>
			<h3>用户名:{{name}}</h3>
			<p>电子邮箱：{{email}}</p>
			<p>创建时间：{{time}}</p>
		
		<mu-button flat  @click="loginOut()"><mu-icon value="exit_to_app" left></mu-icon>退出登录</mu-button>
		</div>
	</mu-container>  
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
			loading:true,
			email:'Null',
			time:'Null',
			userData:'',
			error:''
		}
  },
	mounted(){
		this.getUserInfo()
	},
	computed:{
		name:function(){
			if(localStorage.username) return localStorage.username
		}
	},
	methods:{
		loginOut:function(){
			localStorage.haveLogin = 'no'
			localStorage.username = '未登录'
			window.location.reload();
		},
		getUserInfo:function(){
			let that = this
			let data = localStorage.username
			this.$ajax.get(this.$BaseUrl+'/qian/api/UserViewSet/'+data,
				{timeout:5000},
				).then(function(res){
					that.userData = res.data
				})
				.catch(error => {
					that.error = error
				})
				.finally(() => {			
					this.showResult()
				})
		},
		showResult:function(){
			if(this.userData != ''){
				this.email = this.userData.email
				this.time = this.userData.time
				this.loading = false
			}			
		}		
	}
		
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.idcard{
	padding-top: 20px;
	
}
</style>


