<template>
  <mu-container>
  		
		<mu-form ref="missionFrom" :model="missionFrom" class="mu-demo-form" >
			<mu-form-item label="任务名称" help-text="这有助于对其管理" prop="mission" :rules="misRules">
				<mu-text-field v-model="missionFrom.mission" prop="mission"></mu-text-field>
			</mu-form-item>
			<mu-form-item label="输入http报文" prop="http" :rules="httpRules">
        <mu-text-field type="text" prop="http" v-model="missionFrom.http" multi-line :rows="5" :rows-max="10"></mu-text-field>
			</mu-form-item>		
		</mu-form >
		<mu-button color='primary'  @click="submit()"> 提交<mu-icon value="check_circle" right></mu-icon></mu-button>
		<mu-button color='error'  @click="clear()"> 清空<mu-icon value="delete" right></mu-icon></mu-button>
		<br><br><p v-show="showRes" v-bind:class="{error:error}"><b>{{result.info}}</b></p><br>
		<center><span style="font-size: 0.8rem;color: #9E9E9E;">Tip：在使用前，建议阅读教程</span></center>
		
	</mu-container>  
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
			result:'',
			error:false,
			showRes:false,
			missionFrom:{
				mission:'',
				http:''
			},
			misRules: [
				{ validate: (val) => !!val, message: '必须填写任务名称'}
			],
			httpRules: [
				{ validate: (val) => val.length >= 6, message: '必须填写http报文'}
			],
    }
  },
	methods:{
		clear:function(){
			this.missionFrom.mission = ''
			this.missionFrom.http = ''
		},
		check:function(){
			this.$refs.missionFrom.validate()			
			if(this.missionFrom.mission.length >= 1 && this.missionFrom.http.length >=6 )return true
			else return false
		},
		submit:function(){
			if(this.check()){
				this.error = false
				let that = this
				let data = this.$qs.stringify({
					name:this.missionFrom.mission,
					http:this.missionFrom.http,
					username:localStorage.username
				})
				this.showRes = false
				this.$ajax.post(this.$BaseUrl+'/qian/',	
					data,
					{timeout:5000},
					{emulateJSON:true}
				).then(function(res){
					that.result = res.data
				})
				.catch(error => {
					that.result = error
					that.error = true
				})
				.finally(() => {		
					this.showResult()
				})
			}
		},
		showResult:function(){
			if(this.error == true )this.showRes = true
			else{
				if(this.result.info == '添加失败'){
					this.error = true
					let date = new Date()
					let time = date.getTime() +''
					if(this.$checkTime(time)){
						this.result.info = '添加失败，请重新登录'
					}
					else this.result.info = '添加失败，可能是格式错误，请仔细阅读教程'
				}
			}
			this.showRes = true
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p{
	background-color: #EEEEEE;
	padding:10px 4px;
	font-weight: bold;
	color: #4CAF50;
}
.error{
	
	color: red;
}

.hello{
	height: 100px;
	background-color: peachpuff;
}
</style>


