import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import User from '@/components/User'
import Guide from '@/components/Guide'
import Question from '@/components/Questions'
Vue.use(Router)

const router=new Router({
	//mode:'history',
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
		{
			path: '/Guide',
			name: 'guide',
			component: Guide
		},
		{
			path: '/user',
			name: 'user',
			component: User
		},
		{
			path: '/question',
			name: 'question',
			component: Question
		},
  ]
})
 //router
router.beforeEach((to,from,next)=>{
	if (localStorage.haveLogin == 'yes')next()
	else{
		if(to.path == '/' || to.path == '/Guide' || to.path == '/question' || to.path == '/home')
			next()
		else 
			next('/')
	}
})
export default router;


