<template>
  <mu-container>
		
		 
			 <p style="margin: 5px;color: #0d47a1;">已创建的任务数：</p>
		 
		 <center>
			<mu-circular-progress v-show='loading' class="demo-circular-progress" :size="36" ></mu-circular-progress>
		 	<div id='board' v-show='!loading' class="board">
				<p id='num'>0</p>
			</div>
		 </center>		
		<div class="list">
		</div>
		
      <mu-list v-show='!loading' >

          <mu-list-item v-for='li in list'>	
            <mu-list-item-title>{{li}}</mu-list-item-title>
						<mu-list-item-action>
							
							<mu-icon value="delete" @click='del(li)'></mu-icon>
						</mu-list-item-action>
          </mu-list-item>
				
      </mu-list>
			<div v-show="showchange">
				{{list}}
			</div>
			
			
			<!-- <div v-show="showchange">
				<mu-divider></mu-divider>
				<p style="margin: 5px;color: #0d47a1;">垃圾桶：</p>
				<mu-list>
				
									<mu-list-item v-for='i in changelist'>	
										<mu-list-item-title>{{i}}</mu-list-item-title>
										<mu-list-item-action>
											<mu-tooltip content="取消" >
											<mu-icon value="undo" @click='cancel(i)'></mu-icon></mu-tooltip>
										</mu-list-item-action>
									</mu-list-item>
								
				</mu-list>
				<mu-button flat> <mu-icon value="check_circle" right></mu-icon>提交</mu-button>
			</div> -->
   
		
				
			
		
	</mu-container>  
</template>

<script>
window.btime
window.bn
window.showlistnum = function(){
	if(window.bn < window.n)
	{
			 window.bn++
			document.getElementById('num').innerHTML=window.bn
			if(window.bn == window.n){
				clearInterval(window.btime)
		}
	}
}
export default {
  name: 'setting',
  data () {
    return {
		number:0,
		loading: true,
		errored: false,
		current: 1,
    list:'',
		changelist:[] 
    }
  },
	mounted:function(){
		this.getlist()
		this.show()
	},
	computed:{
		showchange(){
			if(this.changelist.length >=1 )
				return true
			else return false
		}
	},
	methods:{
		show:function(){
			window.bn=0
			window.n = this.list.length
			window.btime = setInterval('window.showlistnum()',50)
		},
		getlist:function(){
			 if(true){
				let that = this
			 	let data = localStorage.username
			 	this.$ajax.get('http://127.0.0.1:8000/qian/api/DataViewSet/'+data,
			 		{timeout:5000}	 		
			 	).then(function(res){
					that.list = res.data		 		
			 	})
			 	.catch(error => {
					this.errored = true
					this.handleErr()
			 	})
				.finally(() => {
					this.loading = false
					//this.showResult()
				})
			 	
			 }
		 },
		handleErr:function(){
			document.getElementById('num').innerHTML = '00'
		},
		cancel:function(i){
			let index
			index = this.changelist.indexOf(i)
			this.list.push(this.changelist[index])
			this.changelist.splice(index,1)
		},
		del:function(i){
			let index
			index = this.list.indexOf(i)
			console.log(this.list[index])
			this.changelist.push(this.list[index])
			this.list.splice(index,1)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	div.board{
		display:table-cell; 
		vertical-align:middle;
		font-size:5rem;
		text-align: center;
		padding: 0px;
		max-height: 8rem;
		width: 20rem;
		max-width: 80%;
		color: #FFFFFF;
		background-color: #03a9f4;
		box-shadow:0px 0px 15px #ccc ;
	}
	p#num{
		margin: 0px;
	}
	div.list{
		width: 85%;
	}

</style>


