<template>
  <div >
		<transition name="open-fade" >
			<section class="word" v-show="showfirst" style="color:#fff;
			padding: 5rem 0 7rem 0;
			background-image: url(../../static/img/b.jpg);
			background-size:cover;
			background-position: center center;">
				
					<div class="intro_wrap" >
						
						<p class="intro_tit" style="font-family:arial ;font-weight: 10;">
							Sign in</p>
						<p class="intro_text">
							无论你在哪里<br>
							Sign 让你从此解放双手<br>
							帮助你完成所有的签到任务
						</p>	
				
					</div>
			
			
			
			</section>
		</transition>
		<section class="word" style="background-color: #fff;">
			<p class="h">这是标题</p>
			<p class="p">这是段落</p>
		</section>
		<section class="img" style="background-image: url(../../static/img/1.jpg);">Divide</section>
		
		<section class="word" style="background-color: #fff;">
			<p class="h">这是标题</p>
			<p class="p">这是段落</p>
		</section>
		
		<section class="img" style="background-image: url(../../static/img/2.jpg);">Divide</section>
		
		<section class="word" style="background-color: #fff;">
			<p class="h">这是标题</p>
			<p class="p">这是段落</p>
		</section>
   <!-- <section class="text">
			<p class="my-tit">建议使用</p>
			</section> -->
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      showfirst: false
    }
  },
	mounted(){
		this.showfirst = true
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	p.h:before{
		content:"◣";
	}
	p.h:after{
		content:"◥";
	}
	.p{
		font-size: 0.9rem;
		letter-spacing: .1rem;
	}
	.h{
		font-size: 1.2rem;
		font-weight: bold;
		color: #03A9F4;
		letter-spacing: .4rem;
	}
	.triangle{
		display: inline-block;
    border-style: solid;
    border-width: 0px 0px 1rem 1rem;
    border-color: transparent transparent blue transparent;
    width: 0px;
    height: 0px;
	}
	section{
		overflow: hidden;
		
	}
section.img {
	font-family: "arial narrow";
	letter-spacing: .2rem;
	padding: 2rem 0;
  color: #FFFFFF;
	margin: 0;
	text-align: center;
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
}
section.word {
  color: #000;
	margin: 0;
	text-align: center;
  padding: 2rem 0;
}
section.text{
	color: #000000;
	margin: 0;
	background-color: #FFFFFF;
	
}
p.my-tit{
	font-weight: bold;
	margin: 0px;
}
div.intro_wrap{
	width: 50%;
	
}
p.intro_tit{
	font-weight: 10;
	font-size: calc(20vh);
	margin: 0px;
}
p.intro_text{
	margin: 0px;
}
transition{
	margin: 0px;
}
.open-fade-enter-active {
  transition: all 5s cubic-bezier(0,1.03,.28,.98);
}
.open-fade-leave-active {
  transition: all 2s cubic-bezier(.55,.07,1,-0.05);
}
.open-fade-enter, .open-fade-leave-to{
/*  transform: translateX(20px); */
  opacity: 0;
}
</style>



