<template>
  <mu-container> 
			<br>
		 
		 <center>
			<div v-show='loading' style="height: 50vh;">
				<mu-circular-progress  class="demo-circular-progress" :size="36" ></mu-circular-progress>
			</div>	
		 </center>

<mu-container v-show='!loading' fluid class="demo-container is-stripe">
	<mu-row gutter>
		<mu-col span="12" sm="12" md="12" lg="6" xl="6">
			<center>
				<p style="margin: 5px;color: #000;">创建的任务</p>
					<div id='board' class="board">
						<span style="font-size: 0.5rem;">任务数</span>
						<p id='num'>0</p>
					</div>
				</center>
				<br>
			<mu-paper  class="demo-paper" :z-depth="1">
					<mu-list >
						<mu-list-item v-for='i in userMissionList' button :ripple="false">	
							<mu-list-item-title>{{i}}</mu-list-item-title>
							<mu-list-item-after-text>我</mu-list-item-after-text>
						</mu-list-item>					
					</mu-list>
			</mu-paper>
		</mu-col>
		
		
		<mu-col span="12" sm="12" md="12" lg="6" xl="6">
			<br>
			<mu-paper  class="demo-paper" :z-depth="1">
				<mu-list >
					<center><p style="margin: 5px;color: #000;font-size:1.2rem;font-weight: bold;">本站所有用户的签到任务</p></center>
					<mu-list-item >	
						<mu-list-item-title><b>任务</b></mu-list-item-title>
							<b>User</b>			
					</mu-list-item>
					
					<mu-divider></mu-divider>
			    <mu-list-item v-for='li in list' button :ripple="false">	
			      <mu-list-item-title>{{li.name}}</mu-list-item-title>
						<mu-list-item-after-text>{{li.user}}</mu-list-item-after-text>
		      </mu-list-item>				
				</mu-list>
			</mu-paper>
		</mu-col>
	</mu-row>
</mu-container>




			<div v-show="showchange">
				<mu-divider></mu-divider>
				<p style="margin: 5px;color: #0d47a1;">垃圾桶：</p>
				<mu-list>
				
									<mu-list-item v-for='i in changelist'>	
										<mu-list-item-title>{{i}}</mu-list-item-title>
										<mu-list-item-action>
											<mu-tooltip content="取消" >
											<mu-icon value="undo" @click='cancel(i)'></mu-icon></mu-tooltip>
										</mu-list-item-action>
									</mu-list-item>
								
				</mu-list>
				<mu-button flat> <mu-icon value="check_circle" right></mu-icon>提交</mu-button>
			</div>
   
		
				
			
		
	</mu-container>  
</template>

<script>
window.btime
window.inc_n //增加
window.showlistnum = function(){
	if(window.inc_n < window.n)
	{
			 window.inc_n++
			document.getElementById('num').innerHTML=window.inc_n
			if(window.inc_n == window.n){
				clearInterval(window.btime)
		}
	}
}
export default {
  name: 'mission',
  data () {
    return {
		number:0,
		loading: true,
		errored: false,
		current: 1,
    list:'',
		username:'Null',
		userMissionList:[],
		changelist:[] 
    }
  },
	mounted:function(){
		this.username = localStorage.username
		this.getlist()
		this.show()
	},
	computed:{
		showchange(){
			if(this.changelist.length >=1 )
				return true
			else return false
		}
	},
	methods:{
		show:function(){
			window.inc_n = 0
			window.n = this.userMissionList.length
			window.btime = setInterval('window.showlistnum()',50)
		},
		getlist:function(){
			 if(true){
				let that = this		 	
			 	this.$ajax.get(this.$BaseUrl+'/qian/api/DataViewSet/',
			 		{timeout:5000}	 		
			 	).then(function(res){
					that.list = res.data		 		
			 	})
			 	.catch(error => {
					this.errored = true
					this.handleErr()
			 	})
				.finally(() => {
					this.userMList()
				})
			 	
			 }
		 },
		userMList:function(){
			if(this.errored == true) this.loading = false
			else {			
				for(let i=0;i<this.list.length;i++){//this.name == this.list[i].user
					if(this.username == this.list[i].user){						
						this.userMissionList.push(this.list[i].name)
					}			
				}
				//console.log(this.userMissionList)
				this.loading = false
				this.show()
			}
			
			
		},
		handleErr:function(){
			document.getElementById('num').innerHTML = '00'
		},
		cancel:function(i){
			let index
			index = this.changelist.indexOf(i)
			this.list.push(this.changelist[index])
			this.changelist.splice(index,1)
		},
		del:function(i){
			let index
			index = this.list.indexOf(i)
			console.log(this.list[index])
			this.changelist.push(this.list[index])
			this.list.splice(index,1)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	div.board{
		text-align: center;
		padding:2rem 0px;
		width: 20rem;
		max-width: 80%;
		color: #FFFFFF;
		background-color: #03a9f4;
		box-shadow:0px 0px 15px #ccc ;
	}
	p#num{
		margin: 0px;
		font-size: 4rem;
	}
	div.list{
		width: 85%;
	}

</style>


