<template>
  <div id="app">
		<transition name="fade">
			<div class='welcomePage' v-if="showWpage" >
			<div class='bg'>
				
			</div>
				<center>
					<div class="wordwrap" style="height: 100%;position: relative;">
						<p style="
						font-family:arial;
						letter-spacing: .2rem;
						padding: 1rem;
						text-decoration: underline;
						margin: 0px;
						font-size: 4rem;
						font-weight: 10;">
						SIGN IN</p>
						<p class="intro_text" style="
						position: relative;
						letter-spacing: .2rem;
						font-weight: bold;">
							无论你在哪里<br>
							Sign 让你从此解放双手<br>
							帮助你完成所有的签到任务
						</p>
						<button class="mybut" @click="goHome()" >
							主页
						</button>
					</div>
					<div class="copyright">©2018 SIGNIN</div>
				</center>
				
			</div>	</transition>
      <div id="app_header" v-bind:class="{headershadow:headershadow}">
				<mu-tooltip content="菜单栏" >
				  <mu-icon size="36" v-on:click='showpanel = !showpanel'  value="account_circle"></mu-icon>
				</mu-tooltip>
			</div>
			<transition name="slide-fade">
			<div class="mask" v-show="smask">
					<div v-show="slogin">
					<mu-flex class="flex-wrapper" justify-content="center">
						<mu-card style="margin: 0 auto;overflow-x: hidden;max-width: 90vw;">
							<mu-flex class="flex-wrapper" justify-content="end">
								<mu-icon value="clear" @click='closemask()'></mu-icon>
							</mu-flex>
							<mu-card-header title="登录" sub-title="登录账号进行下一步操作">
							</mu-card-header>
							
							<mu-container>
								<mu-form ref="formL" :model="LoginFrom" class="mu-demo-form" >
									<mu-form-item label="用户名" prop="username" :rules="usernameRules" icon="account_circle">
										<mu-text-field v-model="LoginFrom.username" prop="username"></mu-text-field>
									</mu-form-item>
									<mu-form-item label="密码" prop="password" :rules="passwordRules" icon="locked">
											<mu-text-field type="password" v-model="LoginFrom.password" prop="password"></mu-text-field>
									</mu-form-item>									
									<mu-form-item>
										<mu-button color="primary" @click="handlelogin()">登录</mu-button>
									</mu-form-item>
									<p>{{loginResult}}</p>
									<mu-alert color="error"  v-show="erroredLogin" @click="erroredLogin = false">
										<mu-icon left value="warning"></mu-icon> 登陆失败(点击关闭)
									</mu-alert>
								</mu-form>
								
								<mu-card-actions>
								<mu-button flat @click="slogin = false;sregister=true">去注册</mu-button>
								
							</mu-card-actions>
							
							</mu-container>							
						</mu-card>
					</mu-flex>
					</div>
				
				<div v-show="sregister">
					<mu-flex class="flex-wrapper" justify-content="center">
						<mu-card style="margin: 0 auto;overflow-x: hidden;max-width: 90vw;">
							<mu-flex class="flex-wrapper" justify-content="end">
								<mu-icon value="clear" @click='closemask()'></mu-icon>
							</mu-flex>
							<mu-card-header title="注册" sub-title="注册一个账号以便进行下一步操作"></mu-card-header>
								<mu-container>
									<mu-form ref="formR" :model="registerForm" class="mu-demo-form" >
										<mu-form-item label="用户名" prop="username" :rules="usernameRules" icon="account_circle">
											<mu-text-field v-model="registerForm.username" prop="username"></mu-text-field>
										</mu-form-item>
										<mu-form-item label="密码" prop="password" :rules="passwordRules" icon="locked">
												<mu-text-field type="password" v-model="registerForm.password" prop="password"></mu-text-field>
										</mu-form-item>
										<mu-form-item label="邮箱"  prop="email" :rules="emailRules" icon="email">
												<mu-text-field  prop="email" v-model="registerForm.email" ></mu-text-field>
										</mu-form-item>
										
										<mu-form-item>
											<mu-button color="primary" @click="handleregister()" >确定</mu-button>
											
										</mu-form-item>
										
									</mu-form>
									<mu-alert color="error"  v-show="erroredReg" @click="erroredReg = false">
											<mu-icon left value="warning"></mu-icon> 注册失败(点击关闭)
									</mu-alert>
								</mu-container>
						
						</mu-card>
					</mu-flex>
				</div>	
			</div>
			</transition>
			<mu-drawer :open.sync="showpanel" :docked="false" :right="true">
				<div v-show="displayInfo" class='info' style="padding: 25px 0;
				text-align:center;
				color:#fff;
				" @click="Goto('/user')">
					<mu-icon value="face" size=56 ></mu-icon><br>
					<span style="font-size: 1.5rem;">已登录:{{user.name}}</span>
					<br><span style="font-size: 0.6rem;">点击进入个人中心</span>
				</div>
				<mu-list>
					<mu-list-item button @click="Goto('/home')" >
						<mu-list-item-action>
							<mu-icon value="home"></mu-icon>
						</mu-list-item-action>
						<mu-list-item-title>主页</mu-list-item-title>
					</mu-list-item>
					
					<mu-list-item button @click="showmask('login')" >
						<mu-list-item-action>
							<mu-icon value="inbox"></mu-icon>
						</mu-list-item-action>
						<mu-list-item-title>登录</mu-list-item-title>
					</mu-list-item>
					
					<mu-list-item button @click="showmask('register')">
						<mu-list-item-action>
							<mu-icon value="add"></mu-icon>
						</mu-list-item-action>
						<mu-list-item-title >注册</mu-list-item-title>
					</mu-list-item>
					
					<mu-list-item button @click="Goto('/Guide')">
						<mu-list-item-action>
							<mu-icon value="mood"></mu-icon>
						</mu-list-item-action>
						<mu-list-item-title>教程</mu-list-item-title>
					</mu-list-item>
					
					<mu-list-item button @click="Goto('/question')">
						<mu-list-item-action>
							<mu-icon value="info"></mu-icon>
						</mu-list-item-action>
						<mu-list-item-title>自问自答</mu-list-item-title>
					</mu-list-item>
					<mu-divider></mu-divider>
					<mu-list-item  @click="showpanel = false" button>
						<mu-list-item-action>
							<mu-icon value="clear"></mu-icon>
						</mu-list-item-action>
						<mu-list-item-title>关闭</mu-list-item-title>
					</mu-list-item>
					
				</mu-list>
			</mu-drawer>
			<mu-dialog title="注册成功" width="360" :open.sync="openSimple">
				现在登陆你的账号吧！
				<mu-button slot="actions" flat color="primary" @click="closeSimpleDialog">确认</mu-button>
			</mu-dialog>
			<router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'App',
  data(){
		return{
			showWpage:false,
			scrollTop: 0,
			id:'',
			pwd:'',
			loginResult:'',
			lData:'',
			rData:'',
			resRsult:'',
			displayInfo:false,
			showpanel:false,
			slogin:false, //s => show
			sregister:false,
			erroredLogin:false,
			erroredReg:false,
			openSimple:false,
			smask:false,
			user:{
				name:'-.-'
			},
			usernameRules: [
        { validate: (val) => !!val, message: '必须填写用户名'},
        { validate: (val) => val.length >= 2, message: '用户名长度太短'}
      ],
      passwordRules: [
        { validate: (val) => !!val, message: '必须填写密码'},
        { validate: (val) => val.length >= 6 && val.length <= 12, message: '密码长度大于6小于12'}
      ],
			emailRules:[
				{validate: function(val){
					let reg = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
					let re = reg.test(val)	
					return re
				} ,message: '请输入正确邮箱格式'}
			],
      registerForm: {
        username: '',
        password: '',
				email:''      
      },
			LoginFrom:{
				username: '',
				password: '',
			}
		}		
	},
	created:function(){
		if(!localStorage.username){
			localStorage.setItem('username','未登录')
			localStorage.setItem('haveLogin','no');
			localStorage.setItem('time', '0')
		}
	},
	 mounted () {
		window.addEventListener('scroll', this.handleScroll)
		if(this.$route.path == '/')this.showWpage = true
		this.user.name = localStorage.username
		let time = new Date()
		let nowTime = time.getTime() + ''
		console.log(nowTime)
		if(localStorage.haveLogin == 'yes' && localStorage.time >  nowTime){
			this.displayInfo=true
		}
		else{
			//let nextTime = time.getTime() + this.Time 
			localStorage.haveLogin = 'no'
			localStorage.username = '未登录'
			localStorage.time = 0
		}		
  },
	computed: {
    headershadow: function () {
      if(this.scrollTop === 0)
				return false
			else return true
    }		
  },
	methods:{
		goHome(){
			this.showWpage = false
			this.Goto('/home')
		},
		closeSimpleDialog(){
			this.openSimple = false
		},
		checkFrom(type){
			if(type=='login'){
				if(this.LoginFrom.username.length >= 2 && this.LoginFrom.password.length >=6 )return true
				else return false
			}
			else if(type=='register'){
				let reg = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/
				let name=this.registerForm.username
				let pw=this.registerForm.password
				let email=this.registerForm.email
				if(name.length>=2 && pw.length >=6 && reg.test(email))return true
				else return false
			}
			else return false
		},
		handle(type){
			if(type == 'login' ){		
				if(this.lData != 'infotype'){						
					this.loginResult = this.lData.info
					if(this.lData.info == '成功'){
						let time = new Date()
						this.user.name =  this.lData.type
						localStorage.haveLogin = 'yes'
						localStorage.username = this.lData.type 
						localStorage.time = time.getTime() + this.$TimeSpan
						this.displayInfo = true
						this.closemask()
						this.showpanel = true
					}			
				}
				else{
					this.loginResult = this.lData.info
				}	
			}
			else if(type == 'reg'){
				if(this.rData.info == 'sign is ok'){
					this.openSimple = true;
					this.sregister = false
					this.slogin = true
				}					
				else{
					this.erroredReg = true
				}
			}
		},
		handlelogin(){
			this.$refs.formL.validate();
      if(this.checkFrom('login')){
				let that = this
				let data = this.$qs.stringify({
				username:this.LoginFrom.username,
				pwd:this.LoginFrom.password
				})
				this.$ajax.post(this.$BaseUrl+'/qian/login/',
					data,
					{timeout:5000}
					// {emulateJSON:true}
				).then(function(res){
						that.lData = res.data
				})
				.catch(error => {
					this.erroredLogin = true								
				})
				.finally(() => {
					that.handle('login')
				})			
			}				
		},
		handleregister(){
			this.$refs.formR.validate()
			if(this.checkFrom('register')){
				let that =this
				let data = this.$qs.stringify({
				username:this.registerForm.username,
				pwd:this.registerForm.password,
				email:this.registerForm.email
				})
				this.$ajax.post(this.$BaseUrl+'/qian/sign/',
					data,
					{timeout:5000},
					{emulateJSON:true}
				).then(function(res){
					that.rData = res.data
				})
				.catch(error => {					
					this.erroredReg = true
				})
				.finally(() => {
					that.handle('reg')
				})
			}	
		},
		
		handleScroll () {
			this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
			
		},
		showmask(target){
			this.showpanel = false
			this.smask = true
			if(target == 'login'){
				this.sregister = false
				this.slogin = true
			}
			else if(target == 'register'){
				this.slogin = false
				this.sregister = true
			}
		},
		closemask(){
			this.smask = false
		},
		Goto(path){
			this.showpanel = false
			this.$router.push(path);
		}
	}
}
</script>
<style scoped="scoped">
	div#app_header{
		width: 100%;
		height: 56px;
		padding: 10px  16px;
		box-sizing: border-box;
		border-width:1px ;
		border-bottom-style:solid;
		border-bottom-color: rgba(0,0,0,0.2);
		top:0px;
		color: #696969;
		position: sticky;
		transition: all 0.6s;
		text-align: right;
		z-index:110;
		margin: 0px;
	}
	div.headershadow{
		background-color: rgba(0,0,0,0.1)!important;
		box-shadow: 0px 0px 5px #000;
		color: #fff!important;
	}
	div.info{
	  background: -webkit-linear-gradient(45deg, #003399, #990066); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(45deg, #003399,  #990066); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(45deg, #003399, #990066); /* Firefox 3.6 - 15 */
    background: linear-gradient(45deg, #003399, #990066);
	}
	div.mask{
		width: 100%;
		height: 100%;
		background-color: rgba(255,255,255,0);
		position: absolute;
		padding:5px 0;
		z-index: 101;
	}
	div.divide{
		height: 1px;
		width: 12rem;
		border: 2px solid #fff;
	}
	button.mybut:hover{
		color:#000;
		background-color: #fff;
	}
	button.mybut{
		-web-kit-appearance:none;
		-moz-appearance: none;
		outline:0;
		box-sizing: border-box;
		text-align:center;
		background-color: rgba(0,0,0,0);
		font-size:1rem;
		border: 2px solid #fff;
		border-radius:5px;
		color:#fff;
		display:block;
		padding:0.2rem 2rem;
		margin: 1em 0em;
		text-decoration:none;
		transition:all 1s;
	}
@media screen and (max-height: 360px){
	div.wordwrap{
		padding: 1.2rem 0;
		animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
		-webkit-animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
	}
}
@media screen and (max-height: 560px) and (min-height:470px){
	div.wordwrap{
		padding: 1.8rem 0;
		animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
		-webkit-animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
	}
}
@media screen and (max-height: 570px) and (min-height:560px){
	div.wordwrap{
		padding: 4rem 0;
		animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
		-webkit-animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
	}
}
@media screen and (max-height: 760px) and (min-height:570px){
	div.wordwrap{
		padding: 6rem 0;
		animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
		-webkit-animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
	}
}
@media screen and (max-height: 1000px) and (min-height:760px){
	div.wordwrap{
		padding: 8rem 0;
		animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
		-webkit-animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
	}
}
@media screen and (min-height:1000px){
	div.wordwrap{
		padding: 12rem 0;
		animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
		-webkit-animation:myfadein 2s cubic-bezier(1,.02,.41,.99);
	}
}
div.welcomePage{
	position: absolute;
	background-image: url(../static/img/bg.jpg);
	background-size: cover;
	background-attachment: fixed;
	background-position: center center;
	color: #fff;width: 100%;
	min-height: 100vh;
	z-index: 500;
}
div.bg{
	position: absolute;
	z-index: -1;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.7);
	animation:bgfadein 3s cubic-bezier(1,.02,.41,.99);
	-webkit-animation:bgfadein 3s cubic-bezier(1,.02,.41,.99); /* Safari and Chrome */
}
div.copyright{
	display: block;
	letter-spacing: .2rem;
	text-align: center;
	margin-top: 8rem;
	
}

#app{
	background-color: rgba(0,0,0,0);
}
@keyframes myfadein
{
	from {color:rgba(0,0,0,0);top:-5px}
	to {color:rgba(255,255,255,1);top:0px}
}
@keyframes bgfadein 
{
	from {background-color:rgba(0,0,0,1);}
	to {background-color:rgba(0,0,0,0.7);}
}
@-webkit-keyframes myfadein /* Safari and Chrome */
{
	from {color:rgba(255,255,255,0);top:-5px}
	to {color:rgba(255,255,255,1);top:0px}
}
@-webkit-keyframes bgfadein /* Safari and Chrome */
{
	from {background-color:rgba(0,0,0,1);}
	to {background-color:rgba(0,0,0,0.7);}
}
.slide-fade-enter-active {
  transition: all .6s cubic-bezier(0,1.03,.28,.98);
}
.slide-fade-leave-active {
  transition: all .4s;
}
.slide-fade-enter, .slide-fade-leave-to{
  transform: translateX(20px);
  opacity: 0;
}
.fade-enter-active {
  transition: all 1s cubic-bezier(0,1.03,.28,.98);
}
.fade-leave-active {
  transition: all .4s;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
	color: rgba(0,0,0,0);
}
</style>


