<template>
	
  <mu-container >
	<mu-card>
		<mu-card-header title="使用教程" sub-title="基于安卓平台, 以百度地图为例:">

		</mu-card-header>
  <div class="demo-vsteper-container">
    <mu-stepper :active-step="vactiveStep" orientation="vertical">
      <mu-step>
        <mu-step-label>
          <b>第一步：抓包</b>
        </mu-step-label>
        <mu-step-content>
          <p class="h">
            1. 打开百度地图到签到页面
          </p>
					<p>如图:</p>
					<img src="../../static/img/doc/1.jpg" alt="">
					
					
					<p class="h">
						2. 打开"无root抓包"选择抓百度地图的包<span class='link' @click="goto()">这里下载</span>
					</p>				
					<img src="../../static/img/doc/2.jpg" alt="">
					
					<p class="h">
						3. 搜索百度地图，选择百度地图开始抓包
					</p>				
					<img src="../../static/img/doc/3.jpg" alt="">
					
					
					<p class="h">
						4. 切换回百度地图完成签到操作
					</p><p>点击签到</p>
					<img src="../../static/img/doc/4.jpg" alt="">
					
					
					<p>直到完成签到操作<br>如图:</p>
					<img src="../../static/img/doc/5.jpg" alt="">
					
					
					
					<p class="h">
						5. 切换回无root抓包,结束抓包</p>
						<p>击红色方块按钮(结束抓包)
					</p>				
					<img src="../../static/img/doc/6.jpg" alt="">
					
					<p class="h">
						6. 找到签到请求，解码选择源码</p>
						<p>
						找到签到请求(最重要的一步)，一般都含有签到字眼如，"签到","打卡"，"sin.."...这个需要自己分辨.
					</p>
					<img src="../../static/img/doc/7.jpg" alt="">				
					
					<br>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">下一步</mu-button>
        </mu-step-content>
      </mu-step>
      <mu-step>			
        <mu-step-label>
          第二步：提交签到任务
        </mu-step-label>
        <mu-step-content>
          <p class="h">
            1. 回到本站，登录账号
          </p>
					
					<p class="h">
						2. 进入个人中心，新建任务
					</p>
					<p>名称:有意义的名称<br>http报文，即上一步全选复制的报文<br>如图</p>
					<img src="../../static/img/doc/8.jpg" alt="">
					
					<p class='h'>
						3. 查看任务
					</p>
					<p>
						进入签到广场，查看自己和其他用户提交的签到任务。
					</p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">下一步</mu-button>
          <mu-button flat class="demo-step-button" @click="vhandlePrev">上一步</mu-button>
        </mu-step-content>
      </mu-step>
			
      <mu-step>
        <mu-step-label>
          完成
        </mu-step-label>
        <mu-step-content>
          <p>
            看完了教程，是不是很简单呢？快去添加任务试一试吧！<br>
						最后，如果使用一段时间后发现没有再继续为你签到，就重新提交一次试试！
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">完成</mu-button>
          <mu-button flat class="demo-step-button" @click="vhandlePrev">上一步</mu-button>
        </mu-step-content>
      </mu-step>
    </mu-stepper>
    <p v-if="vfinished">
      看完啦!<a href="javascript:;" @click="vreset">点这里</a>再看一遍
    </p>
  </div>
	</mu-card>
</mu-container>

</template>

<script>
export default {
  name: 'Guide',
  data () {
    return {
      vactiveStep: 0
    }
  },
	computed: {
    vfinished () {
      return this.vactiveStep > 2;
    }
  },
  methods: {
		goto(){
			 window.open('https://www.baidu.com', '_blank')
		},
    vhandleNext () {
      this.vactiveStep++;
    },
    vhandlePrev () {
      this.vactiveStep--;
    },
    vreset () {
      this.vactiveStep = 0;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
mu-container{
	background-color: rgba(0,0,0,0);
}
.h{
	font-weight: bold;
}
.link{
	text-decoration: underline;
	color: #0000FF;
}
@media screen and (min-width: 360px) {
img{
	width: calc(70% - 36px);
}
}
@media screen and (max-width: 360px) {
    img {
        width: 230px;
    }
}
.step{
	
}
</style>


