<template>
  <div class="wrap">
		<mu-container fluid class="demo-container is-stripe">
		<mu-paper  class="demo-paper" :z-depth="1">
			<mu-tabs :value.sync="active" center inverse color="primary" indicator-color='primary'>
				<mu-tab> <mu-icon value="face"></mu-icon>个人信息</mu-tab>
				<mu-tab> <mu-icon value="create"></mu-icon>添加任务</mu-tab>
				<mu-tab> <mu-icon value="settings"></mu-icon>签到广场</mu-tab>
		</mu-tabs>
		<div class="demo-text" v-if="active === 0">
			<profile></profile>
		</div>
		<div class="demo-text" v-if="active === 1">
			<add></add>
		</div>
		<div class="demo-text" v-if="active === 2">
			<mission></mission>
		</div>
			</mu-paper>
		</mu-container>  
  </div>
</template>

<script>
import profile from './user/Profile.vue'
import add from './user/add.vue'
import setting from './user/setting.vue'
import mission from './user/mission.vue'
export default {
  name: 'User',
	components: { add,setting,profile,mission},
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
			active: 0,
			items:[
				'aaa',
				'bbb',
				'ccc'
			]
    }
  }
}
</script>


<style scoped>
div.list{
	background-color: #FFFFFF;
}
div.demo-text{
	max-height: calc(99vh - 131px);
	overflow-y: scroll;
}
</style>


